* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-size: 10px;
  font-family: monospace;
  background-color: #f1f1f1;
}

#app {
  gap: 16px;
  width: 100vw;
  height: 100vh;
  padding: 10px;
  display: flex;
  flex-wrap: wrap;
  overflow: hidden;
  align-items: space-between;
  justify-content: space-between;
}

.noci {
  height: 50px;
  flex: 0 0 50px;
}

a {
  color: #874170;
}

.footer {
  display: flex;
  bottom: 0;
  width: 100%;
  height: 40px;
  position: fixed;
  font-size: 1.6em;
  align-items: center;
  justify-content: center;
  background-color: #f1f1f1;
  box-shadow: 0px 0px 10px 1px #00000096;
}
